import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController, NavController } from '@ionic/angular';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IProduct } from 'src/app/models/IProduct';
import { IUser } from 'src/app/models/IUser';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';
import { FormValidationService } from 'src/app/services/form-validation.service';

@Component({
  selector: 'app-product-detail-page',
  templateUrl: './product-detail.page.html',
  styleUrls: ['./product-detail.page.scss'],
})
export class ProductDetailPage implements OnInit {

  id: string;
  new: boolean = false;
  user: IUser;
  token: string;
  product: IProduct;
  product_init: IProduct = {
    id: 0,
    name: "",
    weightMax: 0,
    weightMin: 0,
    weightTarget: 0,
    GTIN: "",
    internalCode: "",
    presentation: "",
    speed: 0,
    state: 0
  };
  invalidInput: {
    name: string,
    GTIN: string,
    internalCode: string,
    presentation: string,
    weightMax: string,
    weightMin: string
  };


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private httpClient: HttpService,
    private storageService: StorageService,
    private navCtrl: NavController,
    private alertController: AlertController,
    private formValidations: FormValidationService
  ) { }

  ngOnInit() {
    this.invalidInput = {
      name: '',
      GTIN: '',
      internalCode: '',
      presentation: '',
      weightMax: '',
      weightMin: ''
    };
    this.id = this.activatedRoute.snapshot.paramMap.get('id');
    console.log(this.id)
    if(this.id == null){
      this.new = true;
    }
    console.log("new:", this.new);
    this.storageService.get(AuthConstants.USER).then(res=>{
      console.log("user:", res);
      this.user = res.user;
      this.token = res.accessToken;
    });
    if(!this.new){
      this.httpClient.getProduct(this.id)
        .subscribe(product => {
          console.log(product);
          this.product = product;
        }, error => console.log(error))
    } else {
      this.product = this.product_init;
    }
  }

  async save(){
    let product_temp = this.product;
    delete product_temp.batches;
    delete product_temp.updatedAt;
    // VALIDA LOS ELEMENTOS PASADOS COMO STATE CON LAS REGLAS DE VALIDACION APLICADAS
    let validated = {};
    validated = this.formValidations.validate(
      this.product, 
      [
        { param1: 'name', val: ['empty'] },
        { param1: 'GTIN', param2: 14, val: ['empty', 'only-nums', 'min-length'] },
        { param1: 'internalCode', val: ['empty'] },
        { param1: 'weightMax', param2: 'weightMin', val: ['empty', 'higher'] },
        { param1: 'weightMin', val: ['empty'] },
        { param1: 'presentation', val: ['empty'] }
      ]
    );
    this.invalidInput = {
      ...this.invalidInput,
      ...validated
    }
    // CORROBORA POR ALGUN ELEMENTO CON STRING DE ERROR
    let invalid = false
    for ( let error in this.invalidInput ) {
      if ( this.invalidInput[error] !== '' ) invalid = true;
    }
    if ( !invalid ) {
      // NO HAY ERRORES
      const alert = await this.alertController.create({
        header:'Atención!',
        message: `¿Confirma que desea ${ this.new ? 'crear' : 'modificar'} el producto ${this.product.name}?`,
        buttons: [
          {
            text:'No',
            role:'cancel',
            handler: ()=>{}
          },
          {
            text:'Si',
            handler: () => {
              if(this.new){
                this.httpClient.createProduct(this.product, this.token)
                  .subscribe(res => {
                    console.log(res);
                    this.back();
                }),
                  error => this.showAlert(error)
                
              }
              else {
                console.log(this.product)
                this.httpClient.updateProduct(this.product, this.token).subscribe(res => {
                  console.log(res);
                  this.goHome();
                }), 
                error => this.showAlert(error)
              }
            }
          }
        ]
      });
      await alert.present();
    }
  }

  async delete(){
    const alert = await this.alertController.create({
      header:'Atención!',
      message: `¿Confirma que desea borrar el producto ${this.product.name}?`,
      buttons: [
        {
          text:'No',
          role:'cancel',
          handler: ()=>{}
      },{
          text:'Si',
          handler: () => {
            this.httpClient.deleteProduct(this.product.id, this.token)
              .subscribe(res => {
                console.log(res);
                this.back();
              },
              error => {
                console.log(error);
                this.showAlert(error);
              });
          }
      }]
    });
    await alert.present();
  }
  
  back(){
    this.navCtrl.back();
  }
  
  goHome(): void {
    this.router.navigate(['home']);
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrás' : 'Atrás';
  }

  async showAlert(msg: string){
    const alert = await this.alertController.create({
      header:'Atención!',
      message: msg,
      buttons: [
        {
          text:'OK',
          handler: ()=>{}
      }]
    });
    await alert.present();
  }
}
